'use client';

import Image from "next/image";
import Link from "next/link";

interface ExerciseCardProps {
  title: string;
  description: string;
  difficulty: 'easy' | 'medium' | 'hard';
  imageIndex: number;
  href: string;
}

export default function ExerciseCard({ title, description, difficulty, imageIndex, href }: ExerciseCardProps) {
  const difficultyColors = {
    easy: 'bg-green-100 text-green-800',
    medium: 'bg-yellow-100 text-yellow-800',
    hard: 'bg-red-100 text-red-800'
  };

  return (
    <Link href={href}>
      <div className="maruko-card p-4 cursor-pointer">
        <div className="relative h-48 w-full mb-4">
          <Image
            src={`/practice/images/樱桃小丸子图片生成 (${imageIndex}).png`}
            alt={title}
            fill
            className="rounded-lg object-cover"
          />
        </div>
        <h3 className="text-lg font-bold text-[#FF6B6B] mb-2">{title}</h3>
        <p className="text-gray-600 text-sm mb-4">{description}</p>
        <div className={`inline-block px-3 py-1 rounded-full text-sm ${difficultyColors[difficulty]}`}>
          {difficulty === 'easy' ? '简单' : difficulty === 'medium' ? '中等' : '困难'}
        </div>
        <div className="mt-4 flex justify-end">
          <span className="maruko-button text-sm">开始练习</span>
        </div>
      </div>
    </Link>
  );
} 